import React, { useEffect} from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { fetchVideosData } from '../../store/actions/listVideos-action';
import { listVideosSliceAction } from '../../store/slices/listVideos-slice';
import './scss/_suggestions.scss'
import moment from 'moment/moment';
import FetchContentDetails from '../functionComponent/FetchContentDetails'
import FetchStatistics from '../functionComponent/FetchStatistics'
import FetchChannelIcon from '../functionComponent/FetchChannelIcon';
import { Link } from 'react-router-dom';

function Suggestions() {
  const dispatch = useDispatch()
  const relaledVideos = useSelector(state => state.listVideosSlice.listRelatedVideosState)

  return (
    <div className="videosContainer" style={{
      "marginTop": "0em"
    }}>
      <h1 className='relatedVideosTitle'>Related Videos</h1>
      {
        relaledVideos && relaledVideos.map((element) => {
          return (
            <>
              {
                <div key={element.etag} className="thumbnailCard" >
                  <Link to={`/videoplayer/${element.id.videoId}`} className="thumbnailCardLink"
                    onClick={() => {
                      dispatch(listVideosSliceAction.storeVideoIdReducer(element.id.videoId))
                    }} >

                    <div className="thumbnailAndDuration">
                      <img
                        className='thumbnailImage'
                        src={element.snippet.thumbnails.medium.url}
                        alt=""
                        title={element.snippet.title} />
                      {
                        <p className='duration'>
                          <FetchContentDetails videoId={element.id.videoId} />
                        </p>
                      }
                    </div>

                    <div className="channelIconAndTitle">
                      <div className="channelIcon">
                        <FetchChannelIcon
                          channelId={element.snippet.channelId}
                          title={element.snippet.channelTitle}
                        />
                      </div>

                      <div className="titleAndChannelName">
                        <h4
                          className='title'
                          title={element.snippet.title}>
                          {
                            element.snippet.title.length < 70 ?
                              element.snippet.title :
                              element.snippet.title.slice(0, 67) + "..."
                          }
                        </h4>
                        <p
                          className='channelName smallText'
                          title={element.snippet.channelTitle}>
                          {element.snippet.channelTitle}
                        </p>
                        <p className='viewsAndDate smallText'>
                          <FetchStatistics videoId={element.id.videoId} /> Views・
                          {moment(element.snippet.publishedAt.slice(0, 10)).fromNow()}
                        </p>
                      </div>

                    </div>
                  </Link>
                </div>
              }
            </>
          )
        })
      }
    </div >
  )
}

export default Suggestions