import React, { useEffect } from 'react'
import jwt_decode from 'jwt-decode'
import { useDispatch, useSelector } from 'react-redux'
import { useLocation } from 'react-router-dom';
import { authSliceAction } from '../../store/slices/auth-slice';
import './scss/_logInBtn.scss'


const LogInBtn = () => {

  const dispatch = useDispatch();
  const location = useLocation();
  const user = useSelector(state => state.authSlice.user)
  const logInStatus = useSelector(state => state.authSlice.isLoggedIn)

  useEffect(() => {
    document.getElementById('logInBtn').hidden = logInStatus;
  }, [logInStatus])

  function handleSignOut() {
    dispatch(authSliceAction.setUser({}))
    dispatch(authSliceAction.setLogInStatus(false))
  }

  async function handleCallbackResponse(response) {
    const userObject = await jwt_decode(response.credential)
    dispatch(authSliceAction.setUser(userObject))
    dispatch(authSliceAction.setLogInStatus(true))
  }

  useEffect(() => {
    window.google.accounts.id.initialize({
      client_id: process.env.REACT_APP_AUTH_CLIENT_ID,
      callback: handleCallbackResponse
    })

    location.pathname === '/' && !logInStatus && window.google.accounts.id.prompt()

    window.google.accounts.id.renderButton(
      document.getElementById('logInBtn'),
      {
        type: 'standard',
        theme: 'filled_black',
        size: 'medium',
        text: 'signin',
        shape: 'circle',
        logo_alignment: 'left'
      }
    );
  }, []);

  // Clear cashe and ask to login again
  // window.google.accounts.id.renderButton((notification) => {
  //   if (notification.isNotDisplayed() || notification.isSkippedMoment()) {
  //     document.cookie = `g_state=;path=/;expires=Thu, 01 Jan 1970 00:00:01 GMT`;
  //     window.google.accounts.id.renderButton(
  //       document.getElementById('LogInBtnDiv'),
  //       { theme: 'outline', size: 'large' }
  //     );
  //   }
  // }, [])

  return (
    <div className='logInBtnDiv'>
      <div id="logInBtn"></div>
      {
        Object.keys(user).length !== 0 &&
        <button onClick={() => { handleSignOut() }}>Sign Out</button>
      }
    </div>
  )
}

export default LogInBtn