import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { fetchVideosData } from '../../store/actions/listVideos-action';
import { listVideosSliceAction } from '../../store/slices/listVideos-slice';
import FetchChannelIcon from '../functionComponent/FetchChannelIcon';
import './scss/_videosContainer.scss'
import moment from 'moment';
import numeral from 'numeral';
import FetchContentDetails from '../functionComponent/FetchContentDetails';
import FetchStatistics from '../functionComponent/FetchStatistics';

function VideosContainer() {
  
  const homeVideosData = useSelector(state => state.listVideosSlice.listHomeVideosState);
  const nextPageToken = useSelector(state => state.listVideosSlice.pageToken);
  const activeCategory = useSelector(state => state.listVideosSlice.activeCategory)
  
  const [firstRun, setFirstRun] = useState(true);
  
  const handleOnWheel = () => {
    if (firstRun && window.innerHeight + document.documentElement.scrollTop + 500 >= document.documentElement.scrollHeight) {
      if (activeCategory === 999) {
        dispatch(fetchVideosData("/videos", null, null, nextPageToken, homeVideosData, null));
      } else {
        dispatch(fetchVideosData('/search', null, activeCategory, nextPageToken, homeVideosData, null))
      }
      setFirstRun(false)
    }
  }
  
  const dispatch = useDispatch();
  
  useEffect(() => {
    setFirstRun(true);
  }, [homeVideosData.length, activeCategory])
  
  useEffect(() => {
    if (activeCategory === 999) {
      dispatch(fetchVideosData("/videos", null, null, null, null, null));
    }
  }, [dispatch])

  return (
    <div className="videosContainer" onWheel={() => { handleOnWheel() }}>
      {
        homeVideosData && homeVideosData.map((element) => {
          return (
            <div key={element.id.videoId ?? element.id} className="thumbnailCard">
              <Link to={'/videoplayer'} className="thumbnailCardLink"
                onClick={() => {
                  element.contentDetails
                    ?
                    dispatch(listVideosSliceAction.storeVideoIdReducer(element.id))
                    :
                    dispatch(listVideosSliceAction.storeVideoIdReducer(element.id.videoId))
                }} >

                <div className="thumbnailAndDuration">
                  <img
                    className='thumbnailImage'
                    src={element.snippet.thumbnails.medium.url}
                    alt=""
                    title={element.snippet.title} />
                  {
                    <p className='duration'>
                      {
                        element.contentDetails
                          ?
                          moment.utc((moment.duration(element.contentDetails.duration).asSeconds()) * 1000).format("mm:ss")
                          :
                          <FetchContentDetails videoId={element.id.videoId} />
                      }
                    </p>
                  }
                </div>

                <div className="channelIconAndTitle">
                  <div className="channelIcon">
                    <FetchChannelIcon
                      channelId={element.snippet.channelId}
                      title={element.snippet.channelTitle}
                    />
                  </div>

                  <div className="titleAndChannelName">
                    <h4
                      className='title'
                      title={element.snippet.title}>
                      {
                        element.snippet.title.length < 70 ?
                          element.snippet.title :
                          element.snippet.title.slice(0, 67) + "..."
                      }
                    </h4>
                    <p
                      className='channelName smallText'
                      title={element.snippet.channelTitle}>
                      {element.snippet.channelTitle}
                    </p>
                    <p className='viewsAndDate smallText'>
                      {
                        element.statistics
                          ?
                          numeral(element.statistics.viewCount).format("0.a")
                          :
                          <FetchStatistics videoId={element.id.videoId} />
                      } Views・
                      {moment(element.snippet.publishedAt.slice(0, 10)).fromNow()}
                    </p>
                  </div>

                </div>
              </Link>
            </div>
          )
        })
      }
    </div >
  )
}

export default VideosContainer