import React from 'react'
import './_navbar.scss'
import { useDispatch, useSelector } from 'react-redux';
import { Link, useNavigate } from "react-router-dom";
import ytLogo from '../assets/ytLogo.svg'
import { remountSliceAction } from '../store/slices/remount-slice';
import { searchQuerySliceAction } from '../store/slices/searchQuery-slice';
import LogInBtn from './logInPage/LogInBtn';

function Navbar() {

  const dispatch = useDispatch();
  const navigate = useNavigate();
  const searchQuery = useSelector(state => state.searchQuerySlice.searchQueryState);
  const user = useSelector(state => state.authSlice.user)

  const handleSearchSubmit = (e) => {
    e.preventDefault();
    window.localStorage.setItem('searchQuery', `${searchQuery}`);
    dispatch(searchQuerySliceAction.storeSearchQuery(searchQuery));
    dispatch(remountSliceAction.updateRemountNumber());
    navigate(`searchResults`);
  }

  return (
    <div className="navbar">

      <div className='startNavbar'>
        {/* <i className="fa-solid fa-bars"></i> */}
        <Link to={'/'} className='logoHomeLink'>
          <img src={ytLogo} alt="YouTube" />
          <h3>YouTube Clone</h3>
        </Link>
      </div>

      <form className='midNavbar' onSubmit={handleSearchSubmit}>
        <div>
          <input type="text" placeholder='Search' value={searchQuery} onChange={e => dispatch(searchQuerySliceAction.storeSearchQuery(e.target.value))} />
          {searchQuery && <i className="fa-solid fa-xmark clearSearchTextIcon" onClick={() => {
            dispatch(searchQuerySliceAction.storeSearchQuery(""));
            window.localStorage.setItem('searchQuery', "");
          }}></i>}
          <i className="fa-solid fa-magnifying-glass searchIcon" onClick={handleSearchSubmit}></i>
        </div>
      </form>

      <div className='endNavbar'>
        {
          Object.keys(user).length !== 0 &&
          <img src={user.picture} alt="" />
        }
        <LogInBtn />

      </div>

    </div>
  )
}

export default Navbar