import React, { useEffect } from 'react'
import { useSelector } from 'react-redux'
import './scss/_embededVideo.scss'

const EmbededVideo = () => {

  const videoId = useSelector(state => state.listVideosSlice.videoId)
  window.localStorage.setItem('videoId', `${videoId}`);

  return (
    <div className='embededVideo'>
      {
        videoId &&
        <iframe width="1088" height="612" src={`https://www.youtube.com/embed/${videoId}`} title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowFullScreen></iframe>
      }
    </div>
  )
}

export default EmbededVideo