import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { fetchVideosData } from '../../store/actions/listVideos-action';
import { listVideosSliceAction } from '../../store/slices/listVideos-slice';
// import { overflowBlockSliceActions } from '../../store/slices/overFlowBlock-slice';
import './scss/_categoriesBar.scss';

const CategoriesBar = () => {

  const dispatch = useDispatch();
  const scrollContainer = document.querySelector(".CategoriesBar");

  const res = useSelector(state => state.listVideosSlice.listCategoriesState);

  // const failCategories = ["Short Movies", "Videoblogging", "Foreign", "Sci-Fi/Fantasy", "Shows"];
  // const temp = [];
  // temp.push(res.filter(element => element.snippet.title !== failCategories[0]));

  // for (let i = 0; i < res.length; i++) {
  //   temp[i+1] = temp[i].filter(element => element.snippet.title !== failCategories[i]);
  // }

  // const videoCategories = temp[res.length - 1];

  const videoCategories = res.filter(element =>
    element.snippet.title !== "Short Movies" &&
    element.snippet.title !== "Videoblogging" &&
    element.snippet.title !== "Foreign" &&
    element.snippet.title !== "Sci-Fi/Fantasy" &&
    element.snippet.title !== "Shows"
  )

  // const overFlowBlock = useSelector(state => state.overflowBlockSlice.overflowBlockState);

  // const handleOnWheel = (e) => {
  // dispatch(overflowBlockSliceActions.overflowBlockReducer(true));
  // console.log(overFlowBlock)
  // scrollContainer.scrollLeft += e.deltaY;
  // dispatch(overflowBlockSliceActions.overflowBlockReducer(false));
  // console.log(overFlowBlock)
  // }

  const [toggleRightScrollBtn, setToggleRightScrollBtn] = useState(false);
  setTimeout(() => {
    window.document.getElementById('RBtn').hidden = toggleRightScrollBtn;
  }, 1);

  const [toggleLeftScrollBtn, setToggleLeftScrollBtn] = useState(true);
  setTimeout(() => {
    window.document.getElementById('LBtn').hidden = toggleLeftScrollBtn;
  }, 1);

  const handleOnWheel = () => {

    if (window.innerWidth + scrollContainer.scrollLeft >= scrollContainer.scrollWidth) {
      setToggleRightScrollBtn(true);
    } else {
      setToggleRightScrollBtn(false);
    }

    if (scrollContainer.scrollLeft <= 1) {
      setToggleLeftScrollBtn(true);
    } else {
      setToggleLeftScrollBtn(false);
    }
  }

  const handleOnClickRight = () => {
    scrollContainer.scrollLeft += 1000;
    if (window.innerWidth + scrollContainer.scrollLeft + 1000 >= scrollContainer.scrollWidth) {
      setToggleRightScrollBtn(true);
      setToggleLeftScrollBtn(false);
    } else {
      setToggleRightScrollBtn(false);
      setToggleLeftScrollBtn(false);
    }
  }

  const handleOnClickLeft = () => {
    scrollContainer.scrollLeft -= 1000;
    if (scrollContainer.scrollLeft - 1000 <= 1) {
      setToggleLeftScrollBtn(true);
      setToggleRightScrollBtn(false);
    } else {
      setToggleLeftScrollBtn(false);
      setToggleRightScrollBtn(false);
    }
  }

  const activeCategory = useSelector(state => state.listVideosSlice.activeCategory)

  useEffect(() => {
    dispatch(fetchVideosData("/videoCategories"));
  }, [dispatch])

  const unFilterVideos = () => {
    dispatch(listVideosSliceAction.saveActiveCategoriReducer(999))
    dispatch(fetchVideosData("/videos", null, null, null, null, null));
  }
  
  const filterVideosByCategory = (categoryId) => {
    if (activeCategory !== categoryId) {
      dispatch(listVideosSliceAction.saveActiveCategoriReducer(categoryId))
      dispatch(fetchVideosData('/search', null, categoryId, null, null, null))
    } else {
      unFilterVideos()
    }
  }

  return (
    <div className='CategoriesBar' onWheel={() => { handleOnWheel() }}>
      <button id='LBtn' className='scrollX catBarLeftBtn' onClick={() => { handleOnClickLeft() }}>
        <i className="fa-solid fa-chevron-left"></i>
      </button>
      <li className={activeCategory === 999 ? 'active' : ''} onClick={() => { unFilterVideos() }}>
        All
      </li>

      {
        videoCategories && videoCategories.map((element) => {
          return (
            <li key={element.id} className={activeCategory === element.id ? 'active' : ''} onClick={() => { filterVideosByCategory(element.id) }}>
              {element.snippet.title}
            </li>
          )
        })
      }
      <button id='RBtn' className='scrollX catBarRightBtn' onClick={() => { handleOnClickRight() }}>
        <i className="fa-solid fa-chevron-right"></i>
      </button>
    </div>
  )
}

export default CategoriesBar