import React, { useEffect, useState } from 'react'
import SubscriptionBar from './SubscriptionBar'
import Comments from './Comments'
import Suggestions from './Suggestions'
import EmbededVideo from './EmbededVideo';
import './scss/_videoPlayer.scss'
import { useDispatch, useSelector } from 'react-redux';
import { fetchVideosData } from '../../store/actions/listVideos-action';

function VideoPlayer() {

  //endPoint, searchQuery, categoryId, pageToken, prevData, videoId
  const videoId = useSelector(state => state.listVideosSlice.videoId)
  const nextPageToken = useSelector(state => state.listVideosSlice.pageToken)
  const relaledVideos = useSelector(state => state.listVideosSlice.listRelatedVideosState)
  const [firstRun, setFirstRun] = useState(true);
  const dispatch = useDispatch();

  const handleOnWheel = () => {
    if (firstRun && window.innerHeight + document.documentElement.scrollTop + 100 >= document.documentElement.scrollHeight) {
      dispatch(fetchVideosData("/search", null, null, nextPageToken, relaledVideos, videoId));
      console.log(nextPageToken)
      console.log(relaledVideos)
      setFirstRun(false);
    }
  }

  useEffect(() => {
    dispatch(fetchVideosData("/search", null, null, null, null, videoId))
  }, [dispatch])

  useEffect(() => {
    setFirstRun(true);
  }, [relaledVideos])

  return (
    <div className='videoPlayer' onWheel={() => { handleOnWheel() }}>
      <EmbededVideo />
      <Suggestions />
      {/* <SubscriptionBar /> */}
      {/* <Comments /> */}
    </div>
  )
}

export default VideoPlayer